<template>
  <div class="careerModelBox">
    <div class="careerModelList">
      <div class="careerModel-title">
        <h1>{{careerGuidance.guidanceTitle}}</h1>
      </div>
      <div class="careerModel-tip">
        <p>{{careerGuidance.counselorNo}}</p>
        <p>最后发布于 {{careerGuidance.releaseDate}}</p>
        <p>阅读数 {{careerGuidance.viewNum}}</p>
      </div>
      <div class="careerModel-content" v-html="careerGuidance.content"></div>
      <div class="authorArticleList">
        <h1>作者相关文章</h1>
        <div class="list">
          <p
            v-for="(item,index) in articleList"
            :key="index"
            @click="interGuidanceInfo(item.id)"
            :title="item.guidanceTitle"
          >{{index+1}}、{{item.guidanceTitle}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  interGuidanceInfo,
  updateGuidanceInfo,
  getGuidanceInfo
} from "../../../../../utils/employment.url";
export default {
  data() {
    return {
      careerGuidance: {},
      articleList: []
    };
  },
  methods: {
    //用户信息获取就业指导列表信息
    getGuidanceInfo(counselorNo) {
      getGuidanceInfo({ counselorNo: counselorNo ? counselorNo : "" }, "get")
        .then(res => {
          if (res.status == 200) {
            this.articleList = res.data;
            this.articleList.map((item, index) => {
              item.releaseDate = new Date(item.releaseDate)
                .toLocaleDateString()
                .replace(/\//g, "-");
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //用户信息获取就业指导列表信息
    interGuidanceInfo(id) {
      interGuidanceInfo({ id: id ? id : "" }, "get")
        .then(res => {
          if (res.status == 200) {
            this.getGuidanceInfo(res.data.counselorNo);
            this.careerGuidance = res.data;
            this.careerGuidance.releaseDate = new Date(
              this.careerGuidance.releaseDate
            )
              .toLocaleDateString()
              .replace(/\//g, "-");
            this.updateGuidanceInfo(this.careerGuidance);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //更新就业指导阅读量
    updateGuidanceInfo(params) {
      updateGuidanceInfo(params, "put").then(res => {
        if (res.status == 200) {
        }
      });
    }
  },
  created() {
    this.interGuidanceInfo(this.$route.params.id);
  }
};
</script>
<style lang="scss" scoped>
.careerModelBox {
  width: 1200px;
  margin: 10px auto;
  .careerModelList {
    padding: 25px;
    :nth-of-type(n) {
      margin: 10px auto;
    }
    .careerModel-title {
      font-size: 28px;
      word-wrap: break-word;
      color: #000;
      font-weight: bold;
    }
    .careerModel-tip {
      p {
        display: inline-block;
        color: #858585;
        line-height: 26px;
        margin-right: 50px;
        font-family: "Microsoft YaHei", "SF Pro Display", Roboto, Noto, Arial,
          "PingFang SC", sans-serif;
      }
    }
    .careerModel-content,
    .authorArticleList {
      display: inline-block;
      float: left;
    }
    .careerModel-content {
      width: 70%;
      overflow: hidden;
      line-height: 25px;
      padding-right: 20px;
      border-right: 1px dashed #454545;
      ::v-deep img {
          width: 700px;
        }
    }
    .authorArticleList {
      width: 20%;
      margin-top: 0px;
      margin-left: 20px;
      h1 {
        font-weight: 600;
      }
      .list {
        margin-top: 10px;
        border-top: 1px dashed #454545;
        :nth-of-type(n) {
          margin-top: 10px;
          overflow: hidden; //超出的文本隐藏
          text-overflow: ellipsis; //溢出用省略号显示
          white-space: nowrap; //溢出不换行
          cursor: pointer;
        }
        :hover{
            color: rgb(31, 183, 253)
          }
      }
    }
  }
}
</style>